    public static randomNoRepeats(array: any[]): any {
        let copy: any[] = array.slice(0);
        return () => {
            if (copy.length < 1) { copy = array.slice(0); }
            const index: number = Math.floor(Math.random() * copy.length);
            const item: any = copy[index];
            copy.splice(index, 1);
            return item;
        };
    }
    public static formatShortDate(date: Date): string {
        const day: string = this.dateAddLeadZero(date.getDate());
        const month: string = this.dateAddLeadZero(date.getMonth() + 1);
        const year: number = date.getFullYear();

        return year + "-" + month + "-" + day;
    }
