
import { PnPClientStorage } from '@pnp/common';
export class storage {

    // Function to cache a Promise object in Global Variable (can be used by different components during the page load).
    // The benefit of the function is that as we are storing Promise, then all calls to the cache key will execute the same Promise object - if it is not yet resolved, then would wait until it resolves and return the value.
    // Input parameters:
    //   1) key - cache key
    //   2) promise - reference to the Promise
    public static async getOrPutPromiseAsGlobalVariable(key: string, promise: any): Promise<any> {
        try {
            if (window[key] == null) {
                window[key] = promise();
            }
            return window[key];
        }
        catch (error) {
            console.error(error);
            return null;
        }
    }

    // Function to cache a value from Promise object in Local or Session Storage.
    // This function should be used if we need to cache value that is returned from a Promise (asynchronously). And the same Promise (value) should be used in different components of the same page.
    // The benefit of the function is that as we are storing Promise temporarily as Global Variable. So all calls to the cache key will execute the same Promise object - if it is not yet resolved, then would wait until it resolves and return the value.
    // Using PnP "getOrPut" as basis for the approach - its getter function to wrap provided Promise reference into the function "getOrPutPromiseAsGlobalVariable".
    // Note that had to use Global Variable as temporary storage for Promise object as Local or Session Storages can only contain string values.
    // Input parameters:
    //   1) key - cache key
    //   2) storeName - Local or Session storage
    //   3) promise - reference to the Promise
    //   4) expire - expiry date / time
    public static async getOrPutPromiseValueInStorage(key: string, storeName: "session" | "local", promise: any, expire?: Date): Promise<any> {
        try {
            // Using PnP "getOrPut" as basis for the approach
            const pnpStorage = new PnPClientStorage();
            switch (storeName) {
                case "local":
                    return pnpStorage.local.getOrPut(key, () => this.getOrPutPromiseAsGlobalVariable(key, promise), expire);
                case "session":
                    return pnpStorage.session.getOrPut(key, () => this.getOrPutPromiseAsGlobalVariable(key, promise), expire);                 
                default:
                    return null;
            }
        }
        catch (error) {
            console.error(error);
            return null;
        }
    }

    public static async clearKeyFromLocalStorage(key: string){

        const pnpStorage = new PnPClientStorage();
        try
        {
            pnpStorage.local.delete(key);
        }
        catch(error){
            console.error("clearKeyFromLocalStorage: " + error);
        }

    }
}
