import {
  ApplicationInsights,
  Snippet
} from "@microsoft/applicationinsights-web";

export class TelemetryService {

public static _appInsightsInstance : ApplicationInsights;
public  constructor() {}

// Get Azure App insights Instance Instance for the webpart if already present. If not initialize one.
public static getAppInsightsInstance(aiKey?:string, currentUser?: string): ApplicationInsights {
      if(!TelemetryService._appInsightsInstance){
        TelemetryService._appInsightsInstance = this.InitializeAI(aiKey,currentUser);
      }
      return TelemetryService._appInsightsInstance
  }

  public static InitializeAI(aiKey?:string, currentUser?: string): ApplicationInsights {
        const appInsights = new ApplicationInsights(<Snippet>{
          config: {
            instrumentationKey: aiKey,
            disableFetchTracking: false,
            maxBatchInterval:0,
            enableAutoRouteTracking: true,
            namePrefix:"ExtranetAppViewer"
          }
        });
        //load app insights
        appInsights.loadAppInsights();

        appInsights.context.application.ver = '1.0.1';

        if(currentUser) {
          // one way hash username
          // decode using https://caligatio.github.io/jsSHA
          //const SHA256 = new jsSHA('SHA-256','TEXT');
          //SHA256.update(currentUser);
          //const currentUserHashed = SHA256.getHash('HEX');

          console.log('Pre-hashed current user',currentUser);
          // Encoding is done just for logging for now
          //console.log('Post-hashed current user',currentUserHashed);

          appInsights.setAuthenticatedUserContext(currentUser);
        }

     return appInsights;
  }

}
